.App {
  display: flex;
}
.App nav {
  width: 300px;
  height: 100vh;
}
@media (max-width: 800px) {
  .App nav {
    width: 230px;
  }
}
.App main {
  flex: 1;
  word-wrap: break-word;
  /* word-break: break-all; */
  margin: 1rem;
}

@media (max-width: 600px) {
  .App {
    display: block;
  }
  .App nav {
    width: 100%;
    height: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.main-content .heading {
  font-weight: 100;
  margin-bottom: 2rem;
}
.grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 1rem;
}
@media (max-width: 430px) {
  .grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1rem;
  }
}
.grid-item {
  background-color: rgba(255, 0, 0, 0.151);
  /* background: url("https://images.pexels.com/photos/586744/pexels-photo-586744.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500"); */
  display: flex;
  backdrop-filter: blur(10px);
  align-items: center;
  justify-content: center;
  padding: 2rem;
  border-radius: 1rem;
  min-height: 150px;
  transition: 0.07s;
  text-align: center;
}
.grid-item:hover {
  transform: scale(0.9);
}
.back-btn {
  width: fit-content;
  padding: 4px 12px;
  transition: 0.05s;
}

.back-btn:hover {
  opacity: 0.5;
}
