.nav-con {
  /* background-color: rgb(255, 0, 0); */
  width: 100%;
  height: 100%;
  overflow-y: auto;
  padding: 18px;
  background-color: rgba(255, 255, 255, 0.02);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.nav-con li {
  display: block;
  padding: 8px;
  cursor: default;
  transition: 0.08s;
}

.nav-con li:hover {
  opacity: 0.6;
}

.li-icon {
  width: 30px;
  /* background-color: red; */
}
.nav-active {
  background-color: rgba(255, 0, 0, 0.05);
  border-right: 2px solid red;
  color: red;
}

.nav-active:hover {
  opacity: 1;
}

@media (max-width: 600px) {
  .nav-list {
    display: flex;
  }
  .nav-con li {
    display: inline;
    padding: 16px;
  }

  .li-icon {
    display: none;
  }

  .nav-active {
    border-right: 2px solid transparent;
    border-bottom: 2px solid red;
  }
}
