.about-card-con {
  display: block;
}
.hide-in-small-screen {
  display: block;
}
.about-card-con .img {
  width: 100%;
  height: 30vh;
  background-color: rgb(0, 0, 0);
  border-radius: 12px;
}
.about-card-con .name {
  text-align: center;
  color: red;
  font-weight: bold;
  /* background-color: blue; */
}
.about-card-con .social {
  display: flex;
  align-items: center;
  /* height: 50px; */
  justify-content: space-evenly;
}

.about-card-con .social li {
  display: inline;
  padding: 10px;
}

@media (max-width: 600px) {
  .hide-in-small-screen {
    display: none;
  }
}
